let isTopicArraryReady = false;
let topics = [];
let doc = {};

function fetchDoc(callback) {
  try {
    fetch("assets/docs.json")
      .then( response => response.json())
      .then( json => {
          doc = json;
          callback();
          return;
        }
      );
    return;
  } catch(err) {
    console.log(err);
  }
}

function getAllTopics() {
  for (let i = 0; i < doc.sections.length; i++) {
    let section = doc.sections[i];
    for (let j = 0; j < section.topics.length; j++) {
      let topicObj = section.topics[j];
      topics[topicObj.id] = topicObj;
    }
  }
}

const DocService = {
  init:function(callback) {
    fetchDoc(callback);
  },
  getHeader: function() {
    return doc.header;
  },
  getDoc: function() {
    return doc;
  },
  getSections: function() {
    return doc.sections;
  },
  getTopic: function(topicId) {
    let topic = {};
    if (!isTopicArraryReady) {
      getAllTopics();
      isTopicArraryReady = true;
    }
    topic = topics[topicId];
    return topic;
  }
}

export default DocService;